/* General styles */
body {
    font-family: sans-serif;
    margin: 0;
    padding: 0;
}

h1 {
    text-align: center;
    margin-top: 20px;
}

/* Drop area */
.drop-area {
    border: 2px dashed #007bff;
    padding: 20px;
    margin: 1rem auto;
    width: 50%;
    cursor: pointer;
    text-align: center;
    transition: background-color 0.3s ease;
}

.drop-area:hover {
    background-color: rgba(0, 123, 255, 0.1);
}

.custom-file-button {
    background-color: #4caf50;
    color: white;
    padding: 10px 20px;
    border-radius: 5px;
    cursor: pointer;
    transition: all 0.3s ease;
    font-size: 16px;
    font-weight: bold;
}

.custom-file-button:hover {
    background-color: #45a049;
}

/* Image preview */
.image-preview {
    max-width: 100%;
    height: auto;
    border-radius: 8px;
    box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1);
}

/* Image grid */
.image-grid {
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    gap: 2rem;
    margin-top: 2rem;
}

/* Each image container */
.image-container {
    text-align: center;
}

/* Download button */
.download-btn {
    padding: 0.5rem 1rem;
    font-size: 1rem;
    background-color: #007bff;
    color: white;
    border: none;
    border-radius: 5px;
    cursor: pointer;
    transition: background-color 0.3s ease;
}

.download-btn:hover {
    background-color: #0056b3;
}

/* Error message */
.error {
    color: red;
    margin-top: 1rem;
}

/* Cancel Button */
.cancel-btn {
    display: block;
    margin: 1rem auto;
    background-color: red;
    color: white;
    border: none;
    padding: 0.5rem 1rem;
    cursor: pointer;
    border-radius: 4px;
    transition: background-color 0.3s ease;
}

.cancel-btn:hover {
    background-color: darkred;
}

/* Loading spinner */
.spinner {
    border: 4px solid rgba(0, 0, 0, 0.1);
    width: 36px;
    height: 36px;
    border-radius: 50%;
    border-left-color: #007bff;
    animation: spin 1s ease infinite;
    margin: 20px auto;
}

@keyframes spin {
    to {
        transform: rotate(360deg);
    }
}

/* Responsive */
@media (max-width: 768px) {
    .drop-area {
        width: 80%;
    }
}